body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.header-container .ant-menu-horizontal {
  height: 64px;
  line-height: 64px;
  display: flex;
  justify-content: space-around;
  border-bottom: none;
}


.ant-dropdown-menu-item a i {
  padding-right: 5px;
}

.ant-layout-header {
  background: rgb(255, 255, 255);
}
.list-wrapper {
  background-color: white;
  margin-top: 50px;
}
.admin-info {
  background: #fff;
  margin-bottom: 20px;
}
.card {
}

.admin-info header {
  text-align: center;
  padding: 15px 0 0 0;
  margin-top: 15px;
}
.admin-info header img {
  width: 150px;
  height: 150px;
  max-width: 100%;
  border-radius: 50%;
}
.admin-name {
  text-align: center;
  font-weight: bold;
  padding: 8px 0px;
  font-size: 1.5em;
  font-family: "Monospace Number", "Chinese Quote", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.admin-desc {
  text-align: center;
  padding: 10px 15px 20px 15px;
  font-size: 14px;
}
.tags-wrapper {
  margin-top: 20px;
}

.tags-content {
  padding: 100px;
}
.tags-content .ant-tag {
  margin: 6px;
}
html{
    height:100%;
}
body{
    min-height:100%;
